<template>
    <div>
        <navbar title="设置" v-if="$phonemodel == 'Android'"></navbar>
        <div class="app" :class="{nav:$phonemodel == 'Android'}">
            <ul class="myList">
                <li><a href="javascript: void(0);" @click="edition"><p>当前版本</p><span></span></a></li>
                <li><a href="javascript: void(0);" @click="clearCache"><p>清除缓存</p><span></span></a></li>
            </ul>
            <button class="verify" @click="logout">退出登录</button>
        </div>
    </div>

</template>

<script>
    import navbar from "@/components/navbar";
    import {getVersion} from "../../api/center";
    import { toast, Dialog } from 'vant';
    export default {
        name: "Setting",
        components:{
            navbar
        },
        data()
        {
            return {
                version:'1.4'
            }
        },

        methods:{
            clearCache()
            {
                Dialog.alert({
                    title: '系统提醒',
                    message: '清除缓存成功'
                }).then(() => {

                });
            },
            edition()
            {
                getVersion().then(res => {

                    if (this.$phonemodel == 'Android')
                    {
                        if (res.version != this.version)
                        {
                            Dialog.alert({
                                title: '检测到新版本',
                                message: '最新版'+res.version+', 立即更新'
                            }).then(() => {
                                window.location.href = 'http://shuidihuan.name/download/waterd_Android_apk.apk';
                            });
                        } else
                        {
                            Dialog.alert({
                                title: '当前版本',
                                message: '当前版本号为:'+res.version
                            }).then(() => {

                            });
                        }
                    } else
                    {
                        Dialog.alert({
                            title: '当前版本',
                            message: '当前版本号为:'+res.version
                        }).then(() => {

                        });
                    }

                })

            },
            logout()
            {
                this.$store.dispatch('user/logout').then(() => {
                    this.$router.push('/mine')
                }).catch(() => {

                })
            }
        }
    }
</script>

<style scoped>
      @import "../../static/css/set.css";
</style>



